import { ComponentRef, Directive, ElementRef, Input, OnDestroy, OnInit, Renderer2, TemplateRef } from '@angular/core';
import { Util } from '../../utils/Util';
import { TiPositionType } from '../../utils/Position';
import { TiTipRef, TiTipShowInfo } from '../../services/tip/TiTipInterface';
import { TiTipService } from '../../services/tip/TiTipService';

// TODO:exportAs: 'tiTip' 
/**
 * tip:
 * 1.[TiTipService]{@link ../injectables/TiTipService.html}
 *
 * 2.
 *
 * <example-url>../tiny3demo/#/tip/tip-all</example-url>
 */
@Directive({
  selector: '[tiTip]',
  exportAs: 'tiTip'
})
export class TiTipDirective implements OnInit, OnDestroy {
    /**
     * Tip
     */
    @Input() tiTipPosition?: TiPositionType = 'auto';
   /**
    * 
    *
    */
   @Input() tiTipMaxWidth: string = '276px';
    /**
     * 
     */
    @Input() tiTipHasArrow?: boolean = true;
    /**
     * tiptiptemplateRefComponent
     *
     * 
     */
    @Input() tiTipContext?: any;
    /**
     * tip/
     *
     * 
     */
    @Input() tiTipTrigger?: 'mouse'|'manual' = 'mouse';
    /**
     * @ignore
     * tip
     */
    @Input() tiTipHostEleX: Element;
    protected tipInstance: TiTipRef;
    private hostEle: Element;
    private _tiTip: string | TemplateRef<any>;
    constructor(private tipService: TiTipService,
                hostEleRef: ElementRef) {
        this.hostEle = hostEleRef.nativeElement;
    }

    private static isInValidValue(value: string | TemplateRef<any>): boolean {
        return Util.isUndefined(value) || Util.isNull(value) || value === '';
    }
    ngOnInit(): void {
        // tip
        this.tipInstance = this.tipService.create(this.hostEle, {
            position: this.tiTipPosition,
            maxWidth: this.tiTipMaxWidth,
            hasArrow: this.tiTipHasArrow,
            hostEleX: this.tiTipHostEleX,
            trigger: this.tiTipTrigger || 'mouse', // 'mouse'
            showFn: (): TiTipShowInfo => {
                if (!this.tipInstance || TiTipDirective.isInValidValue(this._tiTip)) {
                    return;
                }

                return { content: this._tiTip, context: this.tiTipContext };
            }
        });
    }
    /**
     * tip
     *
     * string | TemplateRef<any> | any
     *
     * stringv3.0.6XSSv3.0.7XSS
     * v10.1.4html
     */
    @Input()
    // tiptip''undefinedtip
    /**
     * @ignore
     */
    set tiTip(value: string | TemplateRef<any>) {
        this._tiTip = value;
        if (TiTipDirective.isInValidValue(value)) {
            this.hide();
        }
    }
    /**
     * @ignore
     */
    get tiTip(): string | TemplateRef<any> {
        return this._tiTip;
    }
    /**
     * tip
     */
    public show(): ComponentRef<any> {
        if (!this.tipInstance || TiTipDirective.isInValidValue(this._tiTip)) {
            return;
        }

        return this.tipInstance.show(this._tiTip, this.tiTipContext);
    }
    /**
     * tip
     */
    public hide(): void {
        if (this.tipInstance) {
            this.tipInstance.hide();
        }
    }
    // tip
    ngOnDestroy(): void {
        this.tipInstance.hide();
        /* tslint:disable:no-null-keyword */
        this.tipInstance = null;
    }
}
