import { Component, HostListener, ViewEncapsulation } from '@angular/core';
import { TiRadioBaseComponent } from '../base/TiBaseModule';
import { TiBrowser, TiLog, Util } from '../../utils/Util';

/**
 * Checkbox
 *
 * Checkboxinput checkbox
 *
 * AngularCheckboxdisabled, model, click
 *
 * inputTinyUItiCheckbox
 *
 * <example-url>../tiny3demo/#/checkbox/checkbox-all</example-url>
 */
@Component({
      // tslint:disable-next-line:component-selector
      selector: '[tiCheckbox]',
      templateUrl: './checkbox.html',
      styleUrls: ['./checkbox.less']
})
export class TiCheckboxComponent extends TiRadioBaseComponent {
    /**
     * angularindeterminate
     * IEchecked=true indeterminate=truetinycss
     * checkgrouptreeindeterminate=truechecked = false
     * treeTiCheckboxComponent
     * TiCheckboxComponenttree/indeterminate=truechecked = false
     * Chrome/FirefoxcheckedIEcheckedchange
     * checkgroupchange
     * TiCheckboxComponent IEIEclickindeterminatetruefalsechecked = falsechange
     */
    private lastIndeterminate: boolean = undefined;
    ngAfterViewChecked(): void {
        super.ngAfterViewChecked();
        // tree/indeterminate=truechecked = false
        if (this.nativeElement.indeterminate) {
            this.nativeElement.checked = false;
        }
        this.lastIndeterminate = this.nativeElement.indeterminate;
    }
    /**
     * @ignore
     * IEIEclickindeterminatetruefalsechecked = falsechange
     */
    @HostListener('click', ['$event']) public onHostClick(event: MouseEvent): void {
        if (TiBrowser.isIE() && this.lastIndeterminate && !this.nativeElement.indeterminate) {
            this.checked(true);
        }
    }

    /**
     * @ignore
     * checkedindeterminatechange
     * 
     * @protected
     * @param checked true/false/   'indeterminate'null
     */
    protected checked(checked: boolean|'indeterminate'): void {

        const lastChecked: boolean = this.nativeElement.checked;
        const lastIndeterminate: boolean = (this.nativeElement.indeterminate === true) ? true : false; // undefinedfalse
        switch (checked) {
            case true: // 
                this.nativeElement.checked = true;
                this.nativeElement.indeterminate = false;
                break;
            case false: // 
                this.nativeElement.checked = false;
                this.nativeElement.indeterminate = false;
                break;
            case null: // 
            case 'indeterminate': // 
                this.nativeElement.checked = false; // TODO: IEfalse
                this.nativeElement.indeterminate = true;
                break;
            default: // 
                TiLog.error('setChecked(),switch default, can not reach code');
                break;
        }
        // indeterminateundefined
        if (this.nativeElement.checked !== lastChecked || this.nativeElement.indeterminate !== lastIndeterminate) {
            Util.trigger(this.nativeElement, 'change'); // changengModelchange
        }
    }
}