import {
      Component,
      ContentChild,
      ElementRef,
      Input,
      TemplateRef,
      ViewChild
} from '@angular/core';
import { TiDominatorComponent } from '../dominator/TiDominatorModule';
import { TiFormComponent } from '../base/TiBaseModule';
import { TiDropsearchComponent } from '../dropsearch/TiDropsearchModule';
import { TiKeymap } from '../../utils/Util';
/**
 * TagsInput
 *
 * TagsInput
 *
 * <example-url>../tiny3demo/#/tagsinput/tagsinput-all</example-url>
 */
@Component({
      selector: 'ti-tags-input',
      templateUrl: './tagsinput.html',
      styleUrls: ['./tagsinput.less'],
      providers: [TiFormComponent.getValueAccessor(TiTagsInputComponent)]
})

export class TiTagsInputComponent extends TiFormComponent {
      /**
       * 
       */
      @Input() suggestions: Array<any> = [];
      /**
       * ,
       */
      @Input() placeholder: string = '';
      /**
       * ,dominator 'justified' | 'auto'| string
       */
      @Input() panelWidth: string = 'justified';
      /**
       * 
       */
      @Input() labelKey: string = 'label';

      /**
       * @ignore
       * input
       */
      @ViewChild('input', { static: true }) public inputRef: ElementRef;
      /**
       * @ignore
       * item
       */
      @ContentChild(TemplateRef, /* TODO: add static flag */ { static: true }) itemTemplate: TemplateRef<any>;
      /**
       * @ignore
       * donimator
       */
      @ViewChild(TiDominatorComponent, { static: true }) dominatorCom: TiDominatorComponent;
      /**
       * @ignore
       * selectDrop
       */
      @ViewChild(TiDropsearchComponent, { static: true }) selectDrop: TiDropsearchComponent;
      /**
       * @ignore
       * input
       */
      public inputValue: string = ''; // 

      ngAfterViewInit(): void {
            super.ngAfterViewInit();
            // onInit()setFocusableElems(), ngFor/ngIfngAfterViewInit()
            this.setFocusableElems([this.dominatorCom.nativeElement, this.inputRef.nativeElement]);
      }

      /**
       * @ignore
       * select
       */
      onSelect(event: any): void {
            this.model.push(event);
            this.model = this.model.concat();
            this.selectDrop.searchResult = this.getSuggestions(this.suggestions, this.model);

            // 
            this.inputValue = '';
            // 
            setTimeout(() => {
                  this.selectDrop.rePosition();
            }, 0);
      }

      /**
       * @ignore
       * Dominator
       */
      onClickDominator(): void {
            if (this.disabled) {
                  return;
            }

            this.inputRef.nativeElement.focus();
      }

      /**
       * @ignore
       * :
       */
      onDelete(event: { item: any; model: any }): void {
            this.inputRef.nativeElement.focus();
            // 
            setTimeout(() => {
                  this.selectDrop.rePosition();
              }, 0);
            this.selectDrop.searchResult = this.getSuggestions(this.suggestions, event.model);
      }

      /**
       * @ignore
       * 
       */
      onInputBlur(): void {
            this.selectDrop.hide();
            const value: string = this.inputValue;

            // 
            this.inputValue = '';

            // 
            if (value.trim() === '' || this.findFirstIndex(this.model, 'label', value) !== -1) {
                  return;
            }
            // 
            this.addTagToSelected(value);
      }

      /**
       * @ignore
       * input
       */
      onInputFocus(event: FocusEvent): void {
            this.selectDrop.searchResult = this.getSuggestions(this.suggestions, this.model);
            //  
            setTimeout(() => {
                  this.showSelectDrop();
            }, 0);
      }

      /**
       * @ignore
       * inputkeyup
       */
      onInputKeyup(event: KeyboardEvent): void {
            if (event.keyCode === TiKeymap.KEY_ENTER || event.keyCode === TiKeymap.KEY_NUMPAD_ENTER) {
                  this.responseEnter(event);
            }
      }

      /**
       * @ignore
       * inputkeydown
       * keydownkeydownkeyup
       * 
       */
      onInputKeydown(event: KeyboardEvent): void {
            if (event.keyCode === TiKeymap.KEY_BACKSPACE) {
                  this.responseBackspace(event);
            }
      }

      /**
       * @ignore
       * 
       * @param inputValue
       */
      onInputChange(inputValue: string): void {
            // 
            this.selectDrop.searchWordChange(inputValue);
            // 
            this.selectDrop.searchResult = this.getSuggestions(this.selectDrop.searchResult, this.model);
            // 
            if (this.selectDrop.searchResult.length === 0) {
                  this.selectDrop.hide();
            }
      }

      /**
       * inputenter
       * @param event
       */
      private responseEnter(event: KeyboardEvent): void {
            this.selectDrop.hide();
            // 
            const value: string = this.inputValue;
            // input
            this.inputValue = '';

            // 
            if (value.trim() === '' || this.findFirstIndex(this.model, 'label', value) !== -1) {
                  this.showSelectDrop();

                  return;
            }

            // 
            this.addTagToSelected(value);
            // 
            this.selectDrop.searchResult = this.getSuggestions(this.suggestions, this.model);
            this.showSelectDrop();
      }

      /**
       * input
       * @param event
       */
      private responseBackspace(event: KeyboardEvent): void {
            this.selectDrop.hide();
            // 
            if (!this.selectDrop.isShow) {
                  this.showSelectDrop();
                  // 
                  setTimeout(() => {
                        this.selectDrop.rePosition();
                  }, 0);
            }
            const value: string = this.inputRef.nativeElement.value;
            // 
            if (value !== '') {
                  return;
            }

            // 
            const modelLength: number = this.model.length;

            // 
            if (modelLength === 0) {
                  return;
            }

            const lastSelected: any = this.model[modelLength - 1];
            const index: number = this.suggestions.indexOf(lastSelected);
            // 
            this.model.pop();
            this.model = this.model.concat();

            // ;
            // 
            if (index !== -1) {
                  this.selectDrop.searchResult = this.getSuggestions(this.suggestions, this.model);
            }
      }

      /**
       * 
       * @param arr 
       * @param key 
       * @param value 
       * @returns
       */
      private findFirstIndex(arr: any, key: string, value: string): number {
            if (!(arr instanceof Array)) {
                  return -1;
            }

            for (let i: number = 0; i < arr.length; i++) {
                  if (arr[i][key] === value) {
                        return i;
                  }
            }

            return -1;
      }

      /**
       * 
       * @param inputValue 
       */
      private addTagToSelected(inputValue: string): void {
            const index: number = this.findFirstIndex(this.suggestions, 'label', inputValue);
            const newTag: any = index === -1 ? { id: inputValue, label: inputValue } : this.suggestions[index];
            this.model.push(newTag);
            this.model = this.model.concat();
      }

      /**
       * suggestions
       */
      private getSuggestions(suggestions: any, selected: any): Array<any> {
            const newSuggestions: any = !(suggestions instanceof Array) ? [] : suggestions.concat(); // concat

            let index: number;
            for (const select of selected) {
                  index = newSuggestions.indexOf(select);

                  if (index !== -1) { // 
                        newSuggestions.splice(index, 1);
                  }
            }

            return newSuggestions;
      }

      /**
       * @ignore
       * hoverOption
       */
      public showSelectDrop(): void {
            // selectDrop
            this.selectDrop.listCom.model = undefined;

            // 
            if (this.selectDrop.searchResult.length > 0) {
                  this.selectDrop.show();
            }

            // 
            this.selectDrop.listCom.hoverOption = undefined;
      }
}
