import { Component, Input, SimpleChange, SimpleChanges, ViewEncapsulation } from '@angular/core';
import { TiBaseComponent } from '../base/TiBaseModule';

// TODODOM
/**
 *  Icon
 *
 *  IconIcon
 *
 * <example-url>../tiny3demo/#/icon/icon-all</example-url>
 */
@Component({
    selector: 'ti-icon',
    template: '<ng-content></ng-content>',
    styleUrls: ['./icon.less'],
    host: {
        '[class.ti3-icon]': 'true'
    },
    encapsulation: ViewEncapsulation.None
})
export class TiIconComponent extends TiBaseComponent {
    /**
     * icon
     */
    @Input() name: string;

    ngOnChanges(changes: SimpleChanges): void {
        super.ngOnChanges(changes);
        const nameChange: SimpleChange = changes['name'];
        if (nameChange) {
            this.renderer.removeClass(this.nativeElement, `ti3-icon-${nameChange.previousValue}`);
            this.renderer.addClass(this.nativeElement, `ti3-icon-${nameChange.currentValue}`);
        }
    }
}
/* ti-icon
 */
