import { Component, ElementRef, EventEmitter, Inject, Input, Output, Renderer2, ViewEncapsulation } from '@angular/core';
import { TiAccordionComponent } from './TiAccordionComponent';
import { TiBaseComponent } from '../base/TiBaseModule';

/**
 * ti-accordion
 * <example-url>../tiny3demo/#/accordion/acc-all</example-url>
 */
@Component({
    selector: 'ti-accordion-item',
    templateUrl: './accordion-item.html',
    styleUrls: ['./accordion.less'],
    host: {
        '[class.ti3-accordion-panel]': 'true'
    }
})
export class TiAccordionItemComponent extends TiBaseComponent {
    /**
     * 
     */
    @Input() disabled: boolean = false;
    /**
     * head
     *
     * 
     */
    @Input() clickToggle: boolean = true;
    /**
     * 
     */
    @Output() readonly headClick: EventEmitter<Event> = new EventEmitter();

    /**
     * @ignore
     * class,ngClass
     */
    public headClass: string = '';
    /**
     * @ignore
     * body class,ngClass
     */
    public bodyClass: string = '';
    /**
     * @ignore
     * tiAccordion
     */
    constructor(@Inject(TiAccordionComponent) private accordion: TiAccordionComponent,
                hostRef: ElementRef, renderer: Renderer2) {
        super(hostRef, renderer);
    }

    // /set/get
    private isOpen: boolean = false;
    /**
     * false
     *
     */
    @Input()
    /**
     * @ignore
     */
    get open(): boolean {
        return this.isOpen;
    }
    /**
     * @ignore
     */
    set open(value: boolean) {
        if (value !== this.open) {
            if (value) {// openopentrueitem
                this.accordion.closeOtherItems(this);
            }
            this.isOpen = value;
        }
    }

    // item,itemid
    ngOnInit(): void {
        super.ngOnInit();
        this.accordion.addItem(this);
        this.headClass = this.accordion.headClass;
        this.bodyClass = this.accordion.bodyClass;
    }

    // itemitem
    ngOnDestroy(): void {
        super.ngOnDestroy();
        this.accordion.removeItem(this);
    }

    /**
     * @ignore
     * item
     */
    toggleOpen($event: Event): void {
        if (this.disabled) {
            return;
        }
        if (this.clickToggle) {
            this.open = !this.open;
        }
        this.headClick.emit($event);
    }
}
