import { Component, Input, Renderer2, ViewEncapsulation, ElementRef, NgZone, AfterViewInit } from '@angular/core';
import { TiAutofocusComponent } from '../base/TiBaseModule';

/**
 * Button
 *
 * 
 *
 * buttonbuttontiButtontiny buttonbuttonbutton
 *
 * <example-url>../tiny3demo/#/button/button-all</example-url>
 */
@Component({
    // tslint:disable-next-line:component-selector
    selector: '[tiButton]',
    templateUrl: './button.html',
    styleUrls: ['./button.less'],
    host: {
        '[class.ti3-btn-primary]': 'color === "primary"',
        '[class.ti3-btn-danger]': 'color === "danger"',
        '[class.ti3-btn-icon]': 'icon',
        '[class.ti3-btn-onlyIcon]': 'onlyIcon',
        '[class.ti3-btn-large]': 'size === "large"',
        '[class.ti3-btn-middle]': 'size === "middle"',
        '[class.ti3-btn-small]': 'size === "small"',
        '[class.ti3-btn-xs]': 'size === "xs"',
        '[class.ti3-btn-loading]': 'loading'
    },
    encapsulation: ViewEncapsulation.None // button.lessCSS
})
export class TiButtonComponent extends TiAutofocusComponent implements AfterViewInit {
    /**
     * //
     */
    @Input() color:  'default' | 'danger' |'primary' = 'default';
    /**
     * icon
     */
    @Input() icon: boolean = false;
    /**
     * 
     */
    @Input() onlyIcon: boolean = false;
    /**
     * XS/S/M/L M
     */
    @Input() size: 'xs'|'small'|'middle'|'large' = 'middle';
    /**
     * 10.1.4
     *
     * 
     */
    @Input() loading: boolean = false;

     constructor(public renderer: Renderer2, private hostEle: ElementRef, private zone: NgZone) {
        super(hostEle, renderer);
    }

    ngAfterViewInit(): void {
        this.zone.runOutsideAngular(() => {
            this.renderer.listen(this.hostEle.nativeElement, 'mousedown', (): void => {
                this.renderer.setStyle(this.hostEle.nativeElement, 'outline', 'none');
            });
            this.renderer.listen(this.hostEle.nativeElement, 'blur', (): void => {
                this.renderer.setStyle(this.hostEle.nativeElement, 'outline', '');
            });
        });
    }
}
