import { Component, EventEmitter, Input, Output, ElementRef, Renderer2, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { TiBaseComponent } from '../base/TiBaseModule';
import { Util } from '../../utils/Util';


/**
 * Alert
 *
 * <example-url>../tiny3demo/#/alert/alert-all</example-url>
 */
@Component({
  selector: 'ti-alert',
  templateUrl: './alert.html',
  styleUrls: ['./alert.less']
})

export class TiAlertComponent extends TiBaseComponent {
    /**
     * 
     */
    @Input() typeIcon: boolean = true;
    /**
     * 
     */
    @Input() closeIcon: boolean = true;
    /**
     * 
     */
    @Input() dismissOnTimeout: number;
    /**
     * alert
     */
    @Input() triggerScroll: boolean = false;
    /**
     * 
     */
    @Input() type: 'success'|'error'|'warn'|'prompt'|'simple' = 'success';
    /**
     * 
     */
    @Input() size: 'normal' | 'small' = 'normal';
    private isOpen: boolean = true;
    /**
     * 
     */
    @Input() darkTheme: boolean = false;
    /**
     * @ignore
     * 
     */
    @Input() boxShadow:boolean = false;
    /* tslint:disable:no-redundant-jsdoc */
    /**
     * true
     *
     * **open**
     *
     * @typeboolean
     *
     */
    @Input()
    // /openset/get
    /**
     * @ignore
     */
    get open(): boolean {
        return this.isOpen;
    }
    /**
     * @ignore
     */
    set open(value: boolean) {
        if (value === this.open) {
            return;
        }
        if (value) {// true
            this.dismissOnTime();
        } else {// false
            this.clearTimeout();
        }
        // opentiScroll
        this.triggerScrollEvent();
        this.isOpen = value;
    }
    /**
     * open
     */
    @Output() readonly openChange: EventEmitter<Boolean> = new EventEmitter();
    private timeoutId: any; // ID

    constructor(protected hostRef: ElementRef, protected renderer: Renderer2, @Inject(DOCUMENT) private document) {
        super(hostRef, renderer);
    }

    ngOnInit(): void {
        super.ngOnInit();
        // opentrue
        if (this.open) {
            this.dismissOnTime();
        }
    }

    ngOnDestroy(): void {
        if (this.timeoutId !== undefined) {
            clearTimeout(this.timeoutId);
            if (this.open) {
                this.close();
            }
        }
    }

    /**
     * @ignore
     * alert
     */
    dismissOnTime(): void {
        // 
        if (isNaN(this.dismissOnTimeout)) {
            return;
        }
        this.timeoutId = setTimeout(() => {
            this.close();
        }, this.dismissOnTimeout);
    }
    /**
     * @ignore
     * alert
     */
    close(): void {
        this.open = false;
        this.openChange.emit(this.open); // openopen
    }

    /**
     * @ignore
     * alerttiScroll
     */
    triggerScrollEvent(): void {
        if (!this.triggerScroll) {
            return;
        }
        setTimeout(() => {
            Util.trigger(this.document, 'tiScroll');
        }, 0);
    }

    private clearTimeout(): void {
        if (this.timeoutId !== undefined) {
            clearTimeout(this.timeoutId);
        }
    }
}
