import {
    Component,
    ContentChild,
    ElementRef,
    EventEmitter,
    Input,
    IterableChanges,
    IterableDiffer,
    IterableDiffers,
    KeyValueChanges,
    KeyValueDiffer,
    KeyValueDiffers,
    Output,
    Renderer2,
    TemplateRef,
    ViewChild
} from '@angular/core';
import { TiFormComponent } from '../base/TiBaseModule';
import { TiMenuComponent, TiMenuItem } from '../menu/TiMenuModule';
import { TiLocale, TiLocaleWords } from '../../locale/TiLocaleModule';
import { TiPopconfirmConfig } from '../../directives/popconfirm/TiPopconfirmDirective';

/**
 * 10.0.3/9.0.7
 *
 * `TiMenuItem`
 */
export interface TiActionmenuItem extends TiMenuItem {
    /**
     * tiPopconfirm
     *
     * actionmenu
     */
    popConfig?: TiPopconfirmConfig;
}

// 
// children
// 
// childrenmenu

/**
 * ActionMenu
 *
 * ActionMenumenu
 *
 * 
 *
 * ExpressionChangedAfterItHasBeenCheckedError: Expression has changed after it was checked.
 *
 * <example-url>../tiny3demo/#/actionmenu/actionmenu-all</example-url>
 */
@Component({
    selector: 'ti-actionmenu',
    templateUrl: './actionmenu.html',
    styleUrls: ['./actionmenu.less'],
    host: {
        '[style.max-width]': 'maxWidth'
    }
})
export class TiActionmenuComponent extends TiFormComponent {  // BaseCompA
    /* Menustart */
    /**
     * 
     */
    @Input() items: Array<TiActionmenuItem>;
    /**
     * 
     */
    @Input() panelMaxWidth: string = '130px';
    /**
     * 
     */
    @Input() panelMaxHeight: string = '9999px';
    /**
     * key
     */
    @Input() labelKey: string = 'label';
    /**
     * 
     */
    // tslint:disable-next-line:no-output-named-after-standard-event
    @Output() readonly select: EventEmitter<TiActionmenuItem> = new EventEmitter<TiActionmenuItem>();
    /* Menuend */

    /**
     * 
     */
    @Input() maxWidth: string = '250px';
    /**
     * item
     */
    @Input() space: string = '10px';
    // dividerseparatorip
    /**
     * item
     *
     * 3.1.3
     */
    @Input() showDivider: boolean = true;
    /**
     * , 
     */
    @Input() maxShowNum: number = 4;
    /**
     * 
     *
     *  : /more
     */
    @Input() moreText: string; // = '';
    /**
     * 
     *
     *  : /operation
     */
    @Input() menuText: string; // = '';
    /**
     * 
     */
    @Input() data: any;
    /**
     * dataitems(data: any) => Array<MenuItem>
     */
    @Input() dataToItemsFn: (data: any) => Array<TiActionmenuItem>;

    /**
     * @ignore
     * 
     */
    @ContentChild(TemplateRef, /* TODO: add static flag */ { static: true }) firstTemplate: TemplateRef<any>;
    @ContentChild('item', /* TODO: add static flag */ { static: true }) itemTemplate: TemplateRef<any>; //  item 
    @ContentChild('tip', /* TODO: add static flag */ { static: true }) tipTemplate: TemplateRef<any>; //  tip 

    /**
     * @ignore
     */
    @ViewChild(TiMenuComponent, { static: true }) menuCom: TiMenuComponent;
    /**
     * @ignore
     * 
     */
    public panelItems: Array<TiActionmenuItem> = [];
    /**
     * @ignore
     * item
     */
    public halfSpace: string = '';

    private itemsDiffer: IterableDiffer<TiActionmenuItem>;
    private dataDiffer: KeyValueDiffer<any, any>;
    private needInitItems: boolean = false;

    constructor(protected hostRef: ElementRef, protected renderer: Renderer2,
                private iterableDiffers: IterableDiffers, private keyValueDiffers: KeyValueDiffers) {
        super(hostRef, renderer);
        const localeWords: TiLocaleWords = TiLocale.getLocaleWords();
        this.moreText = localeWords.tiActionmenu.more;
        this.menuText = localeWords.tiActionmenu.operation;
    }

    //  IE offsetTop (1px)
    private static isEqualOffsetTop(elem1: any, elem2: any): boolean {
        return Math.abs((elem1 as HTMLElement).offsetTop - (elem2 as HTMLElement).offsetTop) <= 2;
    }

    /**
     * @ignore
     *  
     * @param item 
     */
    public onSelect(item: TiActionmenuItem): void {
        if (!item.disabled) {
            this.select.emit(item);
        }
    }

    ngOnInit(): void {
        super.ngOnInit();
        this.panelItems = this.items;
        this.halfSpace = Number.parseInt(this.space, 10) / 2 + 'px';
    }
    ngDoCheck(): void {
        if (this.data) {
            if (!this.dataDiffer) { // docheckngOnInit()
                this.dataDiffer = this.keyValueDiffers.find(this.data)
                    .create();
            }
            const dataChanges: KeyValueChanges<any, any> | null = this.dataDiffer.diff(this.data);
            if (dataChanges) { // dataitems. diffchanges
                this.items = this.dataToItemsFn(this.data);
            }
        }
        if (this.items) {
            if (!this.itemsDiffer) {
                this.itemsDiffer = this.iterableDiffers.find(this.items)
                    .create();
                this.needInitItems = true;
            }
            const itemsChanges: IterableChanges<TiActionmenuItem> | null = this.itemsDiffer.diff(this.items);
            // diffchangesdiffnullchanges
            if (itemsChanges) { // items
                this.needInitItems = true;
            }
        }
    }
    /**
     * 
     * 10.0.3 
     * #item#tip
     * 
     */
    // tslint:disable-next-line: use-life-cycle-interface
    ngAfterContentInit(): void {
          super.ngAfterContentInit();
          //  item  &&   #tip   item 
          if (!this.itemTemplate && this.firstTemplate
              && (this.firstTemplate.elementRef.nativeElement !== (this.tipTemplate && this.tipTemplate.elementRef.nativeElement))) {
                this.itemTemplate = this.firstTemplate;
          }
    }
    ngAfterViewChecked(): void {
        if (this.needInitItems) {
            this.initItems();
        }
        super.ngAfterViewChecked(); // autofocus
    }
    /**
     * itemitempannelitemmenu
     */
    private initItems(): void {

        this.needInitItems = false;

        const itemElems: HTMLCollection = this.hostRef.nativeElement.getElementsByClassName('ti3-action-menu-item');
        const dividingElems: HTMLCollection = this.hostRef.nativeElement.getElementsByClassName('ti3-action-menu-divider');
        const menuElem: HTMLElement = this.menuCom.nativeElement;
        const moreTextElem: HTMLElement = this.hostRef.nativeElement.getElementsByClassName('tiMoreText')[0];
        const menuTextElem: HTMLElement = this.hostRef.nativeElement.getElementsByClassName('tiMenuText')[0];
        // items
        if (!this.items || this.items.length === 0) {
            this.setDisplay(menuElem, false);

            return;
        } else {
            this.setDisplay(menuElem, true);
        }
        // : itemElemslengthitemslength
        let firstChildrenIndex: number = -1; // 
        // foritem
        for (let i: number = 0; i < this.items.length; i++) {
            if (this.items[i].hasOwnProperty('children')) {
                firstChildrenIndex = i; // 
                // break;
            }
            this.setDisplay(itemElems[i], true); // itemitem
        }

        // menu  //, 
        if (this.items.length <= this.maxShowNum
            && TiActionmenuComponent.isEqualOffsetTop(itemElems[this.items.length - 1], itemElems[0])
            && firstChildrenIndex === -1) {

            for (let i: number = 0; i < this.items.length; i++) {
                this.setAttr(itemElems[i], 'disabled', this.items[i].disabled);
            }
            this.setDisplay(menuElem, false);
            // 
            const focusItems: Array<any> = [];
            for (let i: number = 0; i < this.items.length; i++) {
                focusItems.push(itemElems[i]);
            }
            this.setFocusableElems(focusItems);

            return;
        }
        // Menu//menu3
        if (firstChildrenIndex === -1) {
            firstChildrenIndex = this.items.length; // firstChild
        }
        let outShowNum: number = (this.items.length - 2) < (this.maxShowNum - 1) ? (this.items.length - 2) :
            (this.maxShowNum - 1) < (firstChildrenIndex + 1) ? (this.maxShowNum - 1) : (firstChildrenIndex + 1);

        for (let i: number = this.items.length - 1; i >= 0; i--) {
            if (i > outShowNum - 1) { // 
                //  TODO: removestyle
                this.setDisplay(itemElems[i], false);
                if (dividingElems[i]) {
                    this.setDisplay(dividingElems[i], false);
                }
            } else if (!TiActionmenuComponent.isEqualOffsetTop(menuElem, itemElems[0])) { // 
                this.setDisplay(itemElems[i], false);
                if (dividingElems[i]) {
                    this.setDisplay(dividingElems[i], false);
                }
                outShowNum--;
            } else {
                this.setAttr(itemElems[i], 'disabled', this.items[i].disabled);
            }
        }

        // 
        if (outShowNum === 0) { // 
            this.renderer.setStyle(menuElem, 'margin-left', '0px');
            this.setDisplay(moreTextElem, false);
            this.setDisplay(menuTextElem, true);
            // 
            this.setFocusableElems(this.menuCom.getFocusableElems());
        } else {
            this.setDisplay(moreTextElem, true);
            this.setDisplay(menuTextElem, false);
            // 
            const focusItems: Array<any> = [];
            for (let i: number = 0; i < outShowNum; i++) {
                focusItems.push(itemElems[i]);
            }
            this.setFocusableElems(focusItems.concat(this.menuCom.getFocusableElems()));
        }

        /**
         * 
         * ng-serveExpressionChangedAfterItHasBeenCheckedError, Promise
         */
        Promise.resolve().then(() => {
            this.panelItems = this.items.slice(outShowNum, this.items.length);
        });
    }

    /**
     * /ngIf
     * @param elem HTML
     * @param isShow 
     */
    private setDisplay(elem: any, isShow: boolean): void {
        this.renderer.setStyle(elem, 'display', isShow ? 'inline-block' : 'none');
    }

    /**
     * @ignore
     * ngFor trackByDOMTODOtrackBy?
     * @param index 
     * @param item 
     * @returns 
     */
    public trackByFn(index: any, item: any): any {
        return index;
    }
}
