/**
 * message
 * open({
 *  id:message id,string,idmodal
 *  type:message'prompt' | 'warn' | 'error' | 'confirm'confirm
 *  title:title,stringstringDOM
 *  content:DOM,TemplateRef<any> | Function | string
 *  context:Object
 *  okButton:{
 *    textstring
 *    click: Function
 *    primary: boolean,true,true
 *    autofocus: boolean,true,true
 *    show: boolean,true
 * }
 *  cancleBtn:{
 *    textstring
 *    click: Function
 *    primary: boolean,false,true
 *    autofocus: boolean,false,true
 *    show: boolean,true
 * }
 * })
 */
import { Injectable } from '@angular/core';
import { TiModalRef } from '../modal/TiModalModule';
import { TiModalService } from '../modal/TiModalService';
import { TiMessageComponent } from './TiMessageComponent';
import { TiMessageModule } from './TiMessageModule';
import { TiMessageConfig } from './TiMessageInterface';

/**
 * TiMessageModuleTiMessageService.open(config)
 *
 * <p><span style="color: red">()
 * BrowserAnimationsModule</span>AngularBrowserAnimationsModule
 *  BrowserAnimationsModule BrowserAnimationsModule</p>
 *
 * <example-url>../tiny3demo/#/message/message-all</example-url>
 */
@Injectable({
    providedIn: TiMessageModule
})
export class TiMessageService {
    constructor(private tiModal: TiModalService) {
    }
    /**
     * 
     * [config] 
     *  closecomponentcontent
     */
    public open(config?: TiMessageConfig): TiModalRef {
        const {type, title, content, context, okButton, cancelButton,  ...modalConfig}: TiMessageConfig = config;

        return this.tiModal.open(TiMessageComponent, {
            context: {
                id: config.id,
                type,
                title,
                content,
                context,
                okButton,
                cancelButton
            },
            modalClass: 'ti3-msg-default-width',
            ...modalConfig
        });
    }
}
