import {
  Directive,
  ElementRef,
  Input,
  OnChanges,
  Renderer2,
  SimpleChanges
} from '@angular/core';

import { Util } from '../../utils/Util';

/**
 * @ignore
 * :stringNode(append)
 *
 * angular.jsng-bind-html
 */
@Directive({
  selector: '[tiInclude]'
})
export class TiIncludeDirective implements OnChanges {
    @Input() tiInclude: string | Node; // stringNode
    private element: HTMLElement;
    constructor(private elementRef: ElementRef, private renderer: Renderer2) {
        this.element = this.elementRef.nativeElement;
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes['tiInclude'] && !Util.isUndefined(this.tiInclude)) {
            // 
            if (this.element.childNodes.length > 0) {
                this.element.innerHTML = '';
            }
            //  tiInclude 
            const node: Node = Util.isString(this.tiInclude) ?
                this.renderer.createText(this.tiInclude as string) : this.tiInclude;
            this.renderer.appendChild(this.element, node);
        }
    }
}
