import { Component, EventEmitter, forwardRef, Input, Output, SimpleChanges, Type } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { TiBaseComponent } from './TiBaseComponent';
import { TiBrowser } from '../../utils/Util';

// SSR
declare let global: any;

// TODO: aria-label
/**
 * autofocus tabindex disabled focus() blur() focus/blur/change ControlValueAccessor
 */
@Component({// lib@Component
    selector: 'ti-form',
    template: ''
})
export class TiFormComponent extends TiBaseComponent implements ControlValueAccessor {
    /**
     * HTMLtabindex
     */
    @Input() tabindex: string = '0';
    /**
     * 
     */
    @Input() disabled: boolean; // Angualr[disabled]->setDisabledState
    /**
     * HTMLfocus
     */
    // TODOipHTMLfocus
    // TODOtslint
    // tslint:disable-next-line:no-output-rename
    @Output('focus') readonly focusEvent: EventEmitter<FocusEvent> = new EventEmitter<FocusEvent>();
    /**
     * HTMLblur
     */
    // tslint:disable-next-line:no-output-rename
    @Output('blur') readonly blurEvent: EventEmitter<FocusEvent> = new EventEmitter<FocusEvent>();
    /**
     * @ignore
     * HTMLchange
     */
    // blurmodelchange
    // tslint:disable-next-line:no-output-rename
    @Output('change') readonly changeEvent: EventEmitter<any> = new EventEmitter<any>();

    private focusCancelFns: Array<() => void>; // foucs
    private blurCancelFns: Array<() => void>; // blur
    private mousedownCancelFns: Array<() => void>; // blur
    private focusModel: any; // model

    protected focusElem: any; // autofocus focus()focusableElemsdisabled
    /**
     * focus/blurdisabled
     * input:
     * 1)->=
     * 2)->=
     */
    private focusableElems: Array<any> = [];
    // 41
    private _model: any;  // 
    private ngAfterViewCheckedFirst: boolean = true; // 

protected onModelChange: (model: any) => void; // change
    protected onModelTouched: () => void; // blur
    protected focusedElem: any; // , 
    /**
     * @ignore
     * 
     * @param type 
     * @returns 
     */
    public static getValueAccessor(type: Type<any>): any {
        return {
            provide: NG_VALUE_ACCESSOR,
            // tslint:disable-next-line:no-forward-ref
            useExisting: forwardRef(() => type),
            multi: true
        };
    }
    /**
     * 
     */
    public get focused(): boolean {
        return this.nativeElement.attributes.tiFocused !== undefined;
    }
    /**
     * Dom/disabledfocused
     * @param  element Dom
     * @param  attr 
     * @param  value true,false
     */
    protected setAttr(element: any, attr: string, value: boolean): void {
        // input[disabled]
        // this.renderer.setProperty(this.focusElem, 'disabled', isDisabled); // angulardisabledtruefasle
        // div[disabled]setAttributehtmlsetProperty
        if (value) {
            this.renderer.setAttribute(element, attr, attr);
        } else {
            this.renderer.removeAttribute(element, attr);
        }
    }
    ngOnInit() {
        super.ngOnInit();
        // 
        this.renderer.setAttribute(this.nativeElement, 'tiForm', '');
    }
    /**
     * disabled
     */
    ngOnChanges(changes: SimpleChanges): void {
        super.ngOnChanges(changes);
        if (changes['disabled']) {
            this.setDisabledState(changes['disabled'].currentValue);
        }
    }
    /**
     * Formthis.setFocusableElems([xxx]), super.ngAfterViewChecked()
     * ngAfterViewChecked(): void {
     *      this.setFocusableElems([xxx]);
     *      // super
     *      super.ngAfterViewChecked();
     * }
     */
    ngAfterViewChecked(): void {
        super.ngAfterViewChecked();
        if (this.ngAfterViewCheckedFirst) {
            this.ngAfterViewCheckedFirst = false;
            // autoFocusngAfterViewInitngFor ngIf
            this.autoFocus();
        }
    }
    /**
     * writeValuethis.model= 
     * @param model 
     */
    protected ngOnModelChange(model: any): void {
    }
    /**
     * tab/focus()disabled
     * disabled
     * ngOnInitngFor/ngIfngOnInitngAfterViewInit
     * super.ngOnInit()
     *
     * @param elems Array<Element>disabled
     *
     * 
     * 1
     * this.setFocusableElems([xxxElement, yyyElement]);
     * 2
     * slider/list/menulist/droplist
     * a.@Input() tabindex: string = '0';htmltabindex="0"
     * b.this.setFocusableElems([]);tabindextabindex="1"
     * 3ti-dominator
     * this.setFocusableElems([this.nativeElement]);
     */
    protected setFocusableElems(elems: Array<any>): void {
        if (!elems) {
            //  null undefined[]
            return ;
        }
        // TODO: tabindex
        // TODO: setFocusableElemsactionmenu
        this.focusableElems = elems;
        // 
        this.setFocusElem();
        // tabindex
        this.setElemsTabindex();
        // focus()/blur()
        this.setNativeElementFocusFn();
        // focus/blur
        this.setFocusEvents();
    }
    /**
     *  focus()
     */
    private setFocusElem(): void {
        this.focusElem = undefined;
        // disabled
        for (const elem of this.focusableElems) {
            if (elem && elem.attributes.disabled === undefined) {
                this.focusElem = elem;
                break;
            }
        }
    }
    /**
     * tabindextabindex
     * this.setFocusableElems([this.nativeElement]);tabindex
     * setFocusEvents()
     */
    private setElemsTabindex(): void {
        // tabindex
        this.renderer.removeAttribute(this.nativeElement, 'tabindex');
        this.focusableElems.forEach((elem: any) => {
            // tabindex
            this.setTabindex(elem);
        });
    }
    /**
     * @ignore
     */
    public getFocusableElems(): Array<any> {
        return this.focusableElems;
    }
    /**
     * focus()
     * : document.getElementById('ti-xxx').focus();
     * tiXxxComp.focus();
     */
    private setNativeElementFocusFn(): void {
        // this
        if (this.focusElem !== this.nativeElement) { // 
            // SSRSSRCannot assign to read only property 'focus' of object 
            // ['nativeElement']['focus']
            if (typeof global !== 'undefined') {
                return;
            }
            this.nativeElement.focus = (): void => {
                this.focus();
            };
            this.nativeElement.blur = (): void => {
                this.blur();
            };
        }
    }
    /**
     * 
     */
    private setFocusEvents(): void {
        // if (elems.length === 1 && this.focusableElems && this.focusableElems.length > 1) {
        //     // setFocusElem()elems1
        //     return;
        // }
        // focus/blur
        if (this.focusCancelFns) {
            this.focusCancelFns.forEach((fn: () => void) => fn());
        }
        if (this.blurCancelFns) {
            this.blurCancelFns.forEach((fn: () => void) => fn());
        }
        if (this.mousedownCancelFns) {
            this.mousedownCancelFns.forEach((fn: () => void) => fn());
        }
        // focus/blur
        this.focusCancelFns = new Array<any>();
        this.blurCancelFns = new Array<any>();
        this.mousedownCancelFns = new Array<any>();

        this.focusableElems.forEach((elem: any) => {
            this.focusCancelFns.push(this.renderer.listen(elem, 'focus', this.focusCallbackFn));
            this.blurCancelFns.push(this.renderer.listen(elem, 'blur', this.blurCallbackFn));
        });
    }
    /**
     * focus
     */
    private focusCallbackFn: ((event: FocusEvent) => void) = (event: FocusEvent) => {
        this.focusedElem = event.target;
        // IEevent.relatedTargetevent.relatedTarget
        if (!TiBrowser.isIE()) {
            // 
            if (this.focusableElems.length > 1 &&
                this.focusableElems.includes(event.relatedTarget)) {
                return;
            }
        } else {// IErelatedTargetblur
            if ((event.target as any).tiFocusFrom) {

                // return;
                // 
                if (event.target === (event.target as any).tiFocusFrom) {
                    // IEfocusin

                    return;
                }
                // 
                if (this.focusableElems.length > 1 &&
                    this.focusableElems.includes((event.target as any).tiFocusFrom)) {

                    return;
                }
                // (event.target as any).tiFocusFrom
                // 
                // AB
            }
        }
        // cssfocused
        this.setAttr(this.nativeElement, 'tiFocused', true);
        // 
        this.focusEvent.emit(event);
        // focusmodelblurmodelchange
        this.focusModel = this.model;
    }
    /**
     * blur 
     */
    private blurCallbackFn: ((event: FocusEvent) => void) = (event: FocusEvent) => {
        // IEevent.relatedTargetevent.relatedTarget
        if (!TiBrowser.isIE()) {
            // 
            if (this.focusableElems.length > 1 &&
                this.focusableElems.includes(event.relatedTarget)) {
                return;
            }
        } else {

            // 
            (event.target as any).tiFocusFrom = undefined;

            // 
            if (event.target === document.activeElement) {
                // IEfocusin
                // 
                (document.activeElement as any).tiFocusFrom = event.target;

                return;
            }

            // relatedTarget:IE
            // 
            // ab
            // arelatedTargetblur
            // brelatedTarget
            if (this.focusableElems.length > 1 &&
                this.focusableElems.includes(document.activeElement)) {
                // 
                (document.activeElement as any).tiFocusFrom = event.target; // element setAttribute

                return;
            }
        }
        this.focusedElem = undefined;
        // 
        this.setAttr(this.nativeElement, 'tiFocused', false);
        // 
        this.blurEvent.emit(event);
        // blurmodelchange
        if (this.model !== this.focusModel) {
            this.changeEvent.emit(this.model);
        }
        // touchedtouch
        if (this.onModelTouched) {
            this.onModelTouched();
        }
    }
    /**
     * HTML DOMfocus
     */
    public focus(): void {
        if (this.focusElem) {
            this.focusElem.focus();
        }
    }
    /**
     * HTML DOMblur
     */
    public blur(): void {
        if (this.focusedElem) {
            this.focusedElem.blur(); // 
        }
    }
    /**
     * Tiny autofocus
     * Tiny3autofocusIE9autofocus
     */
    private autoFocus(): void {
        if (this.nativeElement.attributes.autofocus) {
            this.focus();
        }
    }
    /**
     * setget
     */
    get model(): any {
      return this._model;
    }
    /**
     * modelngModelChange
     */
    set model(model: any) {
        if (this._model === model) {
            return;
        }
        this._model = model;
        if (this.onModelChange) {
            this.onModelChange(model);
        }
        this.ngOnModelChange(model);
    }
    /* 4start */
    /**
     * @ignore
     * [ngModel]Angular
     * super.writeValuexxx
     *
     * super.writeValue  this.model=xxx
     * super.writeValuengModelChange
     * this.model=xxxngModelChange
     * @param model any
     */
    writeValue(model: any): void {
        this._model = model;
        this.ngOnModelChange(model);
    }
    /**
     * @ignore
     * Angular(ngModelChange)
     * model
     * @param fn 
     */
    registerOnChange(fn: (model: any) => void): void {
        this.onModelChange = fn;
    }
    /**
     * @ignore
     * Blur
     * @param fn 
     */
    registerOnTouched(fn: () => void): void {
        this.onModelTouched = fn;
    }
    /**
     * @ignore
     * [disabled]
     * ti-xxx[disabled]disableddisabled
     * super.setDisabledState(), 
     * Angular[disabled]checkbox/radiosetDisabledState()[disabled][id]
     * : Form@Input() disabledAngular[disabled]
     * ngOnChangesdisabled
     * @param isDisabled 
     */
    setDisabledState?(isDisabled: boolean): void {
        // this.form.controls.myselect.disable();ngOnChangesthis.disabled =
        this.disabled = isDisabled;
        // disabled
        this.setAttr(this.nativeElement, 'disabled', isDisabled);
        // disabled
        this.focusableElems.forEach((element: any) => {
            this.setAttr(element, 'disabled', isDisabled);
            // divdisabled
            // tabindex
            // less[disabled]ti-xxx[disabled]
            // htmlngClassFormdisabled
            this.setTabindex(element);
        });
    }
    /**
     * tabindex
     * @param element 
     */
    private setTabindex(element: any): void {
        // 
        if (element.attributes.disabled) {
            // 
            this.renderer.removeAttribute(element, 'tabindex');
            // Ainputdisabledtabindex
            // tabindex

            // Atabhref
            if (element.tagName === 'A') {
                this.renderer.removeAttribute(element, 'href');
            }
        } else {
            if (element.tagName === 'A') {
                // Atabindex='0'Chrometabindextab
                if (this.tabindex === '0') {
                    this.renderer.removeAttribute(element, 'tabindex');
                } else {
                    this.renderer.setAttribute(element, 'tabindex', this.tabindex);
                }
                // disabledhref
                if (!element.attributes.href) {
                    // Atinyhrefjavascript:void(0)
                    this.renderer.setAttribute(element, 'href', 'javascript:void(0)');
                }
            } else {
                this.renderer.setAttribute(element, 'tabindex', this.tabindex);
            }
        }
    }
    /* 4end */
}
/**
 * tabindex
 * tabindex<div tabindex="-1">test</div>
 * Tab1,2,3,4...0
 * tabindex='-1'Tab, 
 * AtabindexChrometab
 * Atabhref
 * inputtabindexChrome/tab
 * tabindex='',tabindex='tabindex'Chrome
 */
