import {
  Component,
  DoCheck,
  ElementRef,
  Input,
  OnInit
} from '@angular/core';
import { TiTableComponent } from './TiTableComponent';
/**
 * TiHeadSort ()
 *
 * th/
 *
 * 
 */
@Component({
  selector: 'ti-head-sort',
  templateUrl: './head-sort.html',
  host: {
      '[class.ti3-sort-container]': 'true',
      '[class.ti3-sort-ascent]': 'sortKey === table.dataState.sort.sortKey && clickNum % 3 === 1',
      '[class.ti3-sort-descent]': 'sortKey === table.dataState.sort.sortKey && clickNum % 3 === 2',
      '(click)': 'onClick()'
  }
})

export class TiHeadSortComponent implements OnInit, DoCheck {
    /**
     * 
     *
     * "firstName""firstName"
     */
    @Input() sortKey: string;
    /**
     *  sortKeynull, ()
     */
    @Input() asc: boolean = null;
    /**
     * 
     *
     * a: 
     *
     * b: 
     *
     * sortKey: 
     */
    @Input() compareFn: (a: any, b: any, sortKey?: string) => number; // 
    /**
     * @ignore
     */
    public clickNum: number = 0;
    private hostElement: HTMLElement;
    /**
     * @ignore
     */
    public table: TiTableComponent;
    constructor(private elementRef: ElementRef, table: TiTableComponent) {
        this.hostElement = this.elementRef.nativeElement;
        this.table = table;
    }
    ngOnInit(): void {
        // TiTableComponentenableSort
        this.table.enableSort = true;
        // SSRERROR TypeError: Cannot read property 'sort' of undefined
        // 
        if (this.table.storageId && this.table.storageConfig && this.table.storageConfig.sort && this.table.sessionStorageState && this.table.sessionStorageState.sort) {
            if (this.sortKey === this.table.sessionStorageState.sort.sortKey) {
                this.asc = this.table.sessionStorageState.sort.asc;
            } else {
                this.asc = null;
            }
        }
        if (this.asc !== null) {
            this.clickNum = this.asc ? 0 : 1;
            //  tiTable  tableState  sort 
            this.sort(true);
        }
    }

    ngDoCheck(): void {
        if (this.table.dataState.sort.sortKey !== this.sortKey) {
            this.clickNum = 0;
        }
    }

    /**
     * @ignore
     */
    public onClick(): void {
        if (!this.sortKey) {
            return;
        }
        this.sort(false);
    }

    private sort(isDefaultSort: boolean): void {
        this.clickNum++;
        // this.clickNum3210
        const isDesc: boolean = this.clickNum % 3 === 2 ? true : this.clickNum % 3 === 1 ? false : null;
        this.table.updateSort(this.sortKey, isDesc, isDefaultSort, this.compareFn);
    }
}
