import { Component, ElementRef, HostListener, Input, ViewChild } from '@angular/core';
import { Util } from '../../utils/Util'
import { TiFormComponent } from './TiFormComponent';
// libComponent
/**
 * /
 */
@Component({// lib@Component
    selector: 'ti-radiobase',
    template: ''
})
export class TiRadioBaseComponent extends TiFormComponent {
    /**
     * 
     */
    @Input() label: string; // Tiny3
    /**
     * @ignore
     */
    @ViewChild('labelRef', /* TODO: add static flag */ { static: true }) private labelRef: ElementRef;
    /**
     * @ignore
     */
    @ViewChild('proxyRef', /* TODO: add static flag */ { static: true }) private proxyRef: ElementRef;

    ngOnInit(): void {
        super.ngOnInit();
        this.creatId();
        this.setFocusableElems([this.proxyRef.nativeElement]);
        this.moveNode();
    }

    ngOnDestroy(): void {
        super.ngOnDestroy();
        this.renderer.removeChild(this.renderer.parentNode(this.nativeElement), this.renderer.nextSibling(this.nativeElement));
    }

    /**
     * inputlabel
     */
    private moveNode(): void {
        const parentNode: any = this.renderer.parentNode(this.nativeElement);
        const nextNode: any  = this.renderer.nextSibling(this.nativeElement);
        this.renderer.insertBefore(parentNode, this.labelRef.nativeElement, nextNode);
    }

    // Tiny3 TODO: 0focusproxy
    // @HostListener('focus') onHostFocus() {
    //       Logger.log('onHostFocus');
    //       this.proxyNode.focus();
    //   }
    /**
     * @ignore
     * checkbox-skin
     * radiotab
     * @param event 
     */
    @HostListener('click', ['$event']) public onHostClick(event: MouseEvent): void {
        if (!this.nativeElement.disabled) {
            this.focusElem.focus();
        }
    }

    // inputchange
    // changechange
    // 
    @HostListener('change', ['$event']) public onHostChange(event: Event): void {
        event.stopPropagation();
    }
    /**
     * @ignore
     * checkbox-skincheckbox-labeldom
     * spanlabelforinputinputclickspan/label
     * dom
     * @param event 
     */
    public onLabelClick(event: MouseEvent): void {
        event.stopPropagation();
    }
    /**
     * @ignore
     * (EnterSpace)
     * keyup(keydownkeypress);
     * 
     * keyupkeydown
     * @param event 
     */
    public onSpaceKeydown(event: KeyboardEvent): void { // with type info
        if (!this.nativeElement.disabled) {
            event.preventDefault();
        }
    }
    /**
     * @ignore
     * @param event 
     */
    public onSpaceKeyup(event: KeyboardEvent): void { // with type info
        if (!this.nativeElement.disabled && this.canChange()) {
            this.nativeElement.checked = !this.nativeElement.checked; // 
            // this.cdRef.detectChanges();  checkedchange
            // checkchange"createEvent" in document. bluronchange
            Util.trigger(this.nativeElement, 'change');
        }
    }
    /**
     * @ignore
     * 
     * @returns true
     */
    protected canChange(): boolean {
        return true;
    }
}// end of class

