import { Component, ContentChild, ElementRef, EventEmitter, Input, NgZone, Output, Renderer2,
    TemplateRef } from '@angular/core';
import { TiFormComponent } from '../base/TiBaseModule';
import { Util } from '../../utils/Util';
import { TiPositionType } from '../../utils/Position';

/**
 * @ignore
 */
@Component({
    selector: 'ti-dominator',
    templateUrl: './dominator.html',
    styleUrls: ['./dominator.less'],
    host: {
        '[class.ti3-select-dominator-container]': 'true',
        '[class.ti3-multiselect-container]': 'multiple'
    },
    providers: [TiFormComponent.getValueAccessor(TiDominatorComponent)]
})
export class TiDominatorComponent extends TiFormComponent {

    @Input() labelKey: string; //  TODO: Array
    @Input() placeholder: string; // 
    @Input() buttonStyle: 'dropdown' |'plus' = 'dropdown'; //  or 'dropdown' 'plus'
    @Input() multiple: boolean = false; // 
    @Input() selectedTipPosition: TiPositionType = 'auto'; // tip
    @Input() dropshow: boolean = false; // select
    /**
     * @ignore
     * tagTpSearchbox
     */
    @Input() tagMaxWidth: string;
    /**
     * dominator2, multipletrue
     */
    @Input() maxLine: number = 2 ;
    /**
     * multipletrue
     *
     * 10.1.0
     */
    @Input() showSelectedNumber: boolean = false ;
    /**
     * dominator multipletruemaxLine
     */
    @Input() maxHeight: string;
    @Input() type: 'tagsinput' | string;
    /**
     * 
     *
     * 3.0.8
     */
    @Input() clearable: boolean = false;
    @Output() readonly delete: EventEmitter<any> = new EventEmitter<any>();
    /**
     * 
     *
     * 3.0.8
     */
    @Output() readonly clear: EventEmitter<any> = new EventEmitter<any>();

    @ContentChild(TemplateRef, /* TODO: add static flag */ { static: true }) firstTemplate: TemplateRef<any>;
    @ContentChild('item', /* TODO: add static flag */ { static: true }) itemTemplate: TemplateRef<any>;
    @ContentChild('placeholder') placeholderTemplate: TemplateRef<any>;

    private isMouseIn: boolean = false;
    constructor(render: Renderer2, hostRef: ElementRef, private zone: NgZone) {
        super(hostRef, render);
    }

    ngOnInit(): void {
        super.ngOnInit();
        // tag24px, +2  .ti3-select-dominator-text padding
        // tslint:disable-next-line: binary-expression-operand-order
        this.maxHeight = !this.multiple ? '' : this.maxHeight ? this.maxHeight : (this.maxLine * 24 + 2) + 'px';
    }

    ngAfterViewInit(): void {
        super.ngAfterViewInit();
        this.setFocusableElems([this.nativeElement]);
        this.zone.runOutsideAngular(() => {
            // mousedown
            this.renderer.listen(this.nativeElement, 'mousedown', (event: MouseEvent): void => {
                if (this.disabled) {
                    event.preventDefault(); // click
                }
            });
            if (this.clearable && !this.multiple) {
                this.renderer.listen(this.nativeElement, 'mouseenter', (): void => {
                    if (this.disabled || Util.isEmptyString(this.model)) {
                        return;
                    }
                    this.zone.run(() => {
                        this.isMouseIn = true;
                    });
                });
                this.renderer.listen(this.nativeElement, 'mouseleave', (): void => {
                    if (this.isMouseIn) {
                        this.zone.run(() => {
                            this.isMouseIn = false;
                        });
                    }
                });
            }
        });
    }
    /**
     * 
     * 3.1.2select
     * 2
     * 
     */
    ngAfterContentInit(): void {
        super.ngAfterContentInit();
        // itemfooteritem
        if (!this.itemTemplate && this.firstTemplate !== this.placeholderTemplate) {
            this.itemTemplate = this.firstTemplate;
        }
    }
    /**
     * 
     * @param item
     * @returns
     */
    public onDelete(item: any): void {
        if (item.disabled === true) {
            return;
        }
        // 
        const index: number = this.model.indexOf(item);
        if (index !== -1) {
            this.model.splice(index, 1);
        }
        this.delete.emit({
            item: item,
            model: this.model
        });
        // model
        this.model = this.model.concat();
    }
    /**
     * @ignore
     */
    public onClickClear(event: MouseEvent): void {
        event.stopPropagation();
        this.model = null;
        this.clear.emit();
    }
    public showClear(): boolean {
        return this.clearable && !this.multiple && !this.disabled && (this.isMouseIn || this.focused) && this.model;
    }
}
