import { Directive, ElementRef } from '@angular/core';

/**
 * @ignore
 * : input
 */
@Directive({
    selector: '[tiAutoSelect]'
})

export class TiAutoSelectDirective {
    constructor(private elementRef: ElementRef) {}
    ngOnInit(): void {
        setTimeout(() => {
          this.elementRef.nativeElement.select();
        }, 0);
      }
}