import { Component, ViewEncapsulation } from '@angular/core';
import { TiRadioBaseComponent } from '../base/TiBaseModule';

/**
 * radio
 *
 * radioinputinputTinyUItiRadio
 *
 * <example-url>../tiny3demo/#/radio/radio-all</example-url>
 */
@Component({
    // tslint:disable-next-line:component-selector
    selector: '[tiRadio]', // 
    templateUrl: './radio.html', // 
    styleUrls: ['./radio.less'], // 
    encapsulation: ViewEncapsulation.None // Emulated()NativeNone
})
export class TiRadioComponent extends TiRadioBaseComponent {
    /**
     *  @ignore
     */
    protected canChange(): boolean {
        return !this.nativeElement.checked;
    }
}
