import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';
/**
 * 
 *
 * <h3></h3>
 * <iframe src="../tiny3demo/#/theme/theme-basic" frameborder='0' width="100%" onload="this.height = document.documentElement.clientHeight"></iframe>
 */
export class TiTheme {

    /**
     * CSShead link
     * @param cssPath css
     * @param id link id
     */
    public static loadCss(cssPath: string, id: string): HTMLLinkElement {
        let link: HTMLLinkElement = document.getElementById(id) as HTMLLinkElement;

        // linklink
        if (!link) {
            link = document.createElement('link');
            document.head.appendChild(link);
        }
        // link href
        if (link.href === cssPath) {
            return;
        }
        link.href = cssPath;
        link.id = id;
        link.rel = 'stylesheet';
        link.type = 'text/css';

        // CSSVarlinkdisableddisabled
        if (link.hasAttribute('disabled')) {
            link.removeAttribute('disabled');
        }

        return link;
    }
    /**
     * CSSVar
     * @returns CSSVar
     */
    public static isNativeSupportCssVar(): boolean {
        const isBrowser: boolean = typeof window !== 'undefined';
        const isNativeSupport: boolean = isBrowser && window.CSS && window.CSS.supports && window.CSS.supports('(--a: 0)');

        return isNativeSupport;
    }
    /**
     * AppModule
     * @param app AppModule
     */
    public static bootstrapModule(app: any): void {
        platformBrowserDynamic()
            .bootstrapModule(app)
            .catch((err: any) => console.error(err));
    }
}
