import {
    AfterContentInit,
    AfterViewChecked,
    Component,
    ElementRef } from '@angular/core';
import { TiTabComponent } from './TiTabComponent';
import { Util } from '../../utils/Util';
/**
 * TiTabHeaderComponent 
 *
 */
@Component({
    selector: 'ti-tab-header',
    templateUrl: './tab-head.html',
    host: {
        '[class.ti3-tab-head-no-outline]': 'true'
    }
})

export class TiTabHeaderComponent implements AfterContentInit, AfterViewChecked {
    private oldHeaderWidth: number;
    constructor(private tab: TiTabComponent, private elementRef: ElementRef) {}

    ngAfterContentInit(): void {
        // AfterContentInitTiTabComponentOnInit
        // ti-tab-headerheader
        this.tab.headNode = this.elementRef.nativeElement;
        if (!this.tab.header) {
            // domheader
            this.tab.header =  this.elementRef.nativeElement.innerText;
        }
    }

    ngAfterViewChecked(): void {
        if (!this.tab.tabset.hasSlider) {
            return;
        }
        if (Util.isUndefined(this.oldHeaderWidth)) {
            this.oldHeaderWidth = this.elementRef.nativeElement.offsetWidth;

            return;
        }
        const headerWidth: number = this.elementRef.nativeElement.offsetWidth;
        if (headerWidth !== this.oldHeaderWidth) {
            this.tab.tabset.headerWidthChange = true;
            this.oldHeaderWidth = headerWidth;
        }
    }
}
