import { Component, ElementRef, EventEmitter, Input, OnInit, Output, Renderer2 } from '@angular/core';
import { TiFileInfo, TiFileItem, TiFilter, TiUploadConfig, TiUploadRef } from '../../services/upload/TiFileInterface';
import { TiBaseComponent } from '../base/TiBaseModule';
import { TiLocale, TiLocaleWords } from '../../locale/TiLocaleModule';
import { TiBrowser, Util } from '../../utils/Util';

/**
 * 10.0.2
 *
 * uploadTiBaseComponent
 */
@Component({
    selector: 'ti-uploadbase',
    template: ''
})
export class TiUploadbaseComponent extends TiBaseComponent implements OnInit {

    /**
     * 
     */
    @Input() url: string;
    /**
     * get/post(IE9)
     */
    @Input() method: 'get'|'post' = 'post';
    /**
     * 
     */
    @Input() filters: Array<TiFilter>;
    /**
     * namename
     */
    @Input() alias: string = 'tiFile';
    /**
     * 
     */
    @Input() formData: object;
    /**
     * formDatafileformDatafile
     */
    @Input() formDataFirst: boolean = false;
    /**
     * 
     */
    @Input() autoUpload: boolean = true;
    /**
     *  
     */
    @Input() batchSend: boolean = false;
    /**
     * 
     */
    @Input() headers: object;
    /**
     * IE10+
     *
     * 1.acceptaccept
     *
     * 2.accept
     */
    @Input() accept: string;

    /**
     * 
     */
    @Input() disabled: boolean = false;

    /**
     * @ignore
     */
    title: string = TiBrowser.isChrome() ? ' ' : ''; // tip,Chrometitle""Chrome" ";IE" "tip

    /**
     * 
     *
     * 
     *
     * {
     *
     * file: [TiFileInfo]{@link ../interfaces/TiFileInfo.html}, // 
     *
     * validResults: Array&lt;string&gt;  // name
     *
     * }
     */
    @Output() readonly addItemFailed: EventEmitter<{file: TiFileInfo, validResults: Array<string>}> =
        new EventEmitter<{file: TiFileInfo, validResults: Array<string>}>();
    /**
     * 
     *
     * 
     */
    @Output() readonly addItemSuccess: EventEmitter<TiFileItem> = new EventEmitter<TiFileItem>();
    // url
    /**
     * formData
     * url
     *
     * 
     */
    @Output() readonly beforeSendItems: EventEmitter<Array<TiFileItem>> = new EventEmitter<Array<TiFileItem>>();
    /**
     * 
     *
     * url
     *
     * 
     *
     * {
     *
     * fileItems: Array<[TiFileItem]{@link ../interfaces/TiFileItem.html}>, // 
     *
     * progress: number  // 
     *
     * }
     */
    @Output() readonly progressItems: EventEmitter<{fileItems: Array<TiFileItem>, progress: number}> =
        new EventEmitter<{fileItems: Array<TiFileItem>, progress: number}>();
    /**
     * /
     *
     * url
     *
     * 
     *
     * {
     *
     * fileItems: Array<[TiFileItem]{@link ../interfaces/TiFileItem.html}>, // 
     *
     * response: string  // 
     *
     * status: number 
     *
     * }
     */
    @Output() readonly completeItems: EventEmitter<{fileItems: Array<TiFileItem>, response: string, status: number}> =
        new EventEmitter<{fileItems: Array<TiFileItem>, response: string, status: number}>();
    /**
     * 
     *
     * url
     *
     * 
     *
     * {
     *
     * fileItems: Array<[TiFileItem]{@link ../interfaces/TiFileItem.html}>, // 
     *
     * response: string  // 
     *
     * status: number 
     *
     * }
     */
    @Output() readonly successItems: EventEmitter<{fileItems: Array<TiFileItem>, response: string, status: number}> =
        new EventEmitter<{fileItems: Array<TiFileItem>, response: string, status: number}>();
    /**
     * 
     *
     * url
     *
     * 
     *
     * {
     *
     * fileItems: Array<[TiFileItem]{@link ../interfaces/TiFileItem.html}>, // 
     *
     * response: string  // 
     *
     * status: number 
     *
     * }
     */
    @Output() readonly errorItems: EventEmitter<{fileItems: Array<TiFileItem>, response: string, status: number}> =
        new EventEmitter<{fileItems: Array<TiFileItem>, response: string, status: number}>();
    /**
     * 
     *
     * url
     *
     * 
     *
     * {
     *
     * fileItems: Array<[TiFileItem]{@link ../interfaces/TiFileItem.html}>, // 
     *
     * response: string  // 
     *
     * status: number 
     *
     * }
     */
    @Output() readonly cancelItems: EventEmitter<{fileItems: Array<TiFileItem>, response: string, status: number}> =
        new EventEmitter<{fileItems: Array<TiFileItem>, response: string, status: number}>();
    /**
     * 
     * url
     *
     * 
     */
    @Output() readonly removeItems: EventEmitter<Array<TiFileItem>> = new EventEmitter<Array<TiFileItem>>();
    /**
     * 
     *
     * 
     */
    @Output() readonly completeAllItems: EventEmitter<Array<TiFileItem>> = new EventEmitter<Array<TiFileItem>>();
    /**
     * 
     *
     * 
     */
    @Output() readonly beforeRemoveItems: EventEmitter<Array<TiFileItem>> = new EventEmitter<Array<TiFileItem>>();

    /**
     * 
     */
    public uploadConfig: TiUploadConfig;

    /**
     * @ignore 
     */
    public uploadInst: TiUploadRef;

    /**
     * @ignore 
     */
    // tslint:disable-next-line:typedef
    public uploadLan = (TiLocale.getLocaleWords() as TiLocaleWords).tiUpload;

    constructor(protected hostRef: ElementRef, protected renderer: Renderer2) {
        super(hostRef, renderer);
    }

    ngOnInit(): void {
        super.ngOnInit();
        this.uploadConfig = {
            url: this.url, // 
            method: this.method, // getpost(IE9)
            filters: this.filters, // 
            alias: this.alias, // namename 'tiFile'
            formData: this.formData, // 
            autoUpload: this.autoUpload, // true
            batchSend: this.batchSend || false, // 
            formDataFirst: this.formDataFirst || false, // formDatafile
            headers: this.headers, // 
            onAddItemFailed: (file: TiFileInfo, validResults: Array<string>): void => {
                this.addItemFailed.emit({file, validResults});
            },
            onAddItemSuccess: (fileItem: TiFileItem): void => {
                this.addItemSuccess.emit(fileItem);
            },
            onBeforeSendItems: (fileItems: Array<TiFileItem>): void => {
                this.beforeSendItems.emit(fileItems);
            },
            onProgressItems: (fileItems: Array<TiFileItem>, progress: number): void => {
                this.progressItems.emit({fileItems, progress});
            },
            onCompleteItems: (fileItems: Array<TiFileItem>, response: string, status: number): void => {
                this.completeItems.emit({fileItems, response, status});
            },
            onSuccessItems: (fileItems: Array<TiFileItem>, response: string, status: number): void => {
                this.successItems.emit({fileItems, response, status});
            },
            onErrorItems: (fileItems: Array<TiFileItem>, response: string, status: number): void => {
                this.errorItems.emit({fileItems, response, status});
            },
            onCancelItems: (fileItems: Array<TiFileItem>, response: string, status: number): void  => {
                this.cancelItems.emit({fileItems, response, status});
            },
            onRemoveItems: (fileItems: Array<TiFileItem>): void => {
                this.removeItems.emit(fileItems);
            },
            onBeforeRemoveItems: (fileItems: Array<TiFileItem>): void => {
                this.beforeRemoveItems.emit(fileItems);
            },
            onCompleteAllItems: (fileItems: Array<TiFileItem>): void => {
                this.completeAllItems.emit(fileItems);
            }
        };
        // H5,accept,type
        // acceptacceptaccept
        if (Util.isUndefined(this.accept) && this.filters && this.filters.length) {
            this.filters.forEach((filter: TiFilter) => {
                if (filter.name === 'type') {
                    this.accept = filter.params[0];
                }

                return;
            });
        }
    }

    /**
     * 
     */
    public upload(): void {
        this.uploadInst.uploadAll();
    }

}
