import {
    Component,
    ElementRef,
    Input,
    ViewChild
} from '@angular/core';
import { TiSelectComponent } from '../select/TiSelectModule';
import { TiHeadFilterDropComponent } from './TiHeadFilterDropComponent';
import { TiFormComponent } from '../base/TiBaseModule';
import { TiBrowser } from '../../utils/Util';

// lib
// @dynamic
/**
 * TiHeadFilter ()  th 
 *
 * ti-head-filter 
 *  select 
 *
 * ### 
 * **Inputs**
 *
 * |         |     |    |   |
 * | --------   | :-----   | :---- | :---- |
 * | options   | array  | [] |  |
 * | ngModel   | object  | false |  headfilter |
 * | labelKey   |  string  | 'label' | , 10.0.2 |
 * | panelMaxHeight   |  string  | 8 |  |
 * | id   | string  | undefined | HTMLidid |
 * | select   | function  |  |  |
 * | multiple   |  boolean  | false |  |
 * | selectAll   |  boolean  | false |  |
 * | panelAlign   |  string  | 'left' | 9.0.4 |
 * | searchable   |  boolean  | false | 10.0.2 |
 * | panelWidth   |  'auto'  string  | 'auto' | "auto"()"200px", 10.0.2 |
 * | noDataText   |  string  | '...' | 10.0.3 |
 *
 * <p><span style="color: red"></span>5Input1Output
 * Input(TiSelectComponent,select)</p>
 */
@Component({
    selector: 'ti-head-filter',
    templateUrl: './head-filter.html',
    styleUrls:['./head-filter.less'],
    providers: [TiFormComponent.getValueAccessor(TiHeadFilterComponent)],
    host: {
        '[class.ti3-head-filter-container]': 'true',
        '(blur)': 'onBlur()'
    }
})
export class TiHeadFilterComponent extends TiSelectComponent {
    /**
     * 
     *
     * 9.0.4
     */
    @Input() panelAlign: 'left' | 'right' = 'left';
    // headfilterselectpanelWidthpanelWidth
    /**
     * 
     *
     * 1."auto": 
     *
     * 2.: (Select)"200px"
     *
     * 10.0.2'auto'10.1.2<b></b>180px
     */
    // undefinedpanelWidth
    @Input() panelWidth: 'auto'| string = undefined;
    /**
     * @ignore
     */
    @ViewChild('filterIcon', { static: true }) filterIconEleRef: ElementRef;
    /**
     * @ignore
     */
    @ViewChild(TiHeadFilterDropComponent, { static: true }) dropsearchCom: TiHeadFilterDropComponent;

    ngAfterViewChecked(): void {
        // Selectdominator
        // TODO: select
        if (this.searchable !== this.oldSearchable) {
            this.oldSearchable = this.searchable;
            if (this.searchable) {
                this.setFocusableElems([this.filterIconEleRef.nativeElement]
                    .concat(this.dropsearchCom.getFocusableElems()));
            } else {
                this.setFocusableElems([this.filterIconEleRef.nativeElement]);
            }
        }
    }

    /**
     * @ignore
     * 
     */
    public onBlur(): void {
        super.onBlur();
        if (TiBrowser.isIE() && this.searchable) {
            if (getComputedStyle(this.filterIconEleRef.nativeElement).outlineStyle === 'none') {
                this.renderer.setStyle(this.filterIconEleRef.nativeElement, 'outlineStyle', '');
            }
        }
    }
}
